.App {
  text-align: center;
}

.App-logo {
  height: 22vmin; /* Decrease the size of the logo */
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  display: inline-block;
  margin: 0 10px; /* Horizontal spacing between buttons */
}

.App-button {
  height: 50px; /* Adjust the size as needed */
  width: auto;
  display: block;
}

.App-button-play {
  width: 150px; /* Custom width for the Play Store button */
}

.Swiss-made-button {
  height: 50px; /* Adjust size as needed */
  width: auto;
  margin-left: 8px;
}

.screenshots {
  display: flex;
  justify-content: center;
  margin-top: 20px; /* Space above the screenshots */
}

.Screenshot {
  height: 550px; /* Adjust the height as needed */
  width: auto;
  margin: 0 10px; /* Space between the screenshots */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Optional shadow for better visual appeal */
  margin-bottom: 20px; /* Add margin to the bottom of the screenshots */
}

.App-small-text {
  font-size: calc(14px + 1vmin); /* Smaller text size for the smaller text */
}

.App-margin-small {
  margin-top: 0px; /* Adjust margin to decrease space between the two sections */
}

.Dummy-section ul {
  list-style-type: disc; /* Default bullet points */
  padding-left: 20px; /* Indent the list */
  text-align: left; /* Align list items to the left */
  max-width: 800px; /* Limit the width of the list */
  margin: 0 auto; /* Center the list */
}

.Dummy-section li {
  font-size: 1.2rem; /* Font size for list items */
  margin-bottom: 10px; /* Space between list items */
}

/* New Apple-style section */
.Apple-style-section {
  background-color: #f0f0f5; /* Light grey background */
  padding: 60px 20px; /* Add some padding */
  color: #333; /* Dark text color for contrast */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif;
}

.Apple-style-section h2 {
  font-size: 2.2rem; /* Heading size */
  margin-bottom: 40px; /* Space below heading */
  text-align: center; /* Center the title */
}

/* Container for the two feature boxes */
.feature-container {
  display: flex;
  justify-content: space-around; /* Space between the two boxes */
  align-items: flex-start; /* Align the top of the boxes */
  max-width: 1200px; /* Limit the width of the container */
  margin: 0 auto; /* Center the container */
}

/* Flexbox for each feature box */
.feature-box {
  display: flex;
  align-items: flex-start; /* Align image and text to the top */
  margin-bottom: 40px; /* Space below boxes */
  width: 48%; /* Set width to take more space per box */
}

.feature-image {
  width: 200px; /* Larger width for the image */
  height: auto;
  margin-right: 20px; /* Space between the image and text */
}

.feature-text {
  text-align: left; /* Align text to the left */
  max-width: 350px; /* Make the text part wider */
}

.feature-text h3 {
  font-size: 1.5rem; /* Heading size for the text */
  margin-bottom: 10px; /* Space below subheading */
}

.feature-text ul {
  list-style-type: disc; /* Default bullet points */
  padding-left: 20px; /* Indent the list */
  margin-top: 0; /* Remove default margin */
}

.feature-text li {
  font-size: 1rem; /* Font size for list items */
  line-height: 1.5; /* Line height for readability */
  margin-bottom: 10px; /* Space between bullet points */
}
